<template>
  <transition name="slide">
    <div class="rating_page">
      <head-top head-title="会员中心" :go-back='true'></head-top>
      <section v-if="userInfo">
        <p class="buy_for">为账户 <span>{{userInfo.username}}</span>购买会员</p>
        <section class="vip_prerogative">
          <yd-cell-group>
            <yd-cell-item arrow type="link" href="/vipcard/vipDescription">
              <span slot="left">会员特权</span>
              <span slot="right">会员说明</span>
            </yd-cell-item>
          </yd-cell-group>
          <section class="vip_detail">
            <div class="vip_detail_left">
              <img src="../../images/sheng.jpeg" height="80" width="70">
            </div>
            <div class="vip_detail_right">
              <h4>减免配送费</h4>
              <p>每月减免30单，每日可减免3单，每单最高减4元</p>
              <p>蜂鸟专送专享</p>
            </div>
          </section>
          <section class="vip_detail">
            <div class="vip_detail_left">
              <img src="../../images/jifen.jpeg" height="80" width="70">
            </div>
            <div class="vip_detail_right">
              <h4>减免配送费</h4>
              <p>每月减免30单，每日可减免3单，每单最高减4元</p>
              <p>蜂鸟专送专享</p>
            </div>
          </section>
        </section>
         <section class="apply_vip">
            <header class="header_style">
              <span class="header_left">开通会员</span>
            </header>
           <section class="apply_vip_buy">
             <div class="apply_vip_buy_left">
               <span>1个月</span>
               <span> ¥20</span>
             </div>
             <div class="apply_vip_buy_right" @click="buyCart">购买</div>
           </section>
         </section>
        <yd-cell-group>
          <yd-cell-item arrow type="link" href="/vipcard/useCart">
            <span slot="left">兑换会员</span>
            <span slot="right">使用卡号卡密</span>
          </yd-cell-item>
          <yd-cell-item arrow type="link" href="/vipcard/invoiceRecord">
            <span slot="left">购买记录</span>
            <span slot="right">开发票</span>
          </yd-cell-item>
        </yd-cell-group>
      </section>
     <!-- 可以对进入路由的组件定制切换动画，或者在组件里面定义切换的动画-->
      <transition name="router-slid" mode="out-in">
        <router-view></router-view>
      </transition>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import headTop from 'components/header/head'
  import {mapGetters,mapMutations} from 'vuex'
  export default {
    data(){
      return{
      }
    },
    created(){
    },
    mounted(){
    },
    computed: {
      ...mapGetters([
        'userInfo'
      ]),
    },
    methods: {
      ...mapMutations([
        'ORDER_SUCCESS', 'BUY_CART'
      ]),
      buyCart(){
        this.ORDER_SUCCESS({order_id: '399525134200981325'});
        this.BUY_CART(20);
        this.$router.push('/confirmOrder/payment');
      }
    },
    components:{
      headTop
    }
  }
</script>
<style scoped lang="scss" rel="stylesheet/scss">
  @import '../../../src/style/mixin';
  .rating_page{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 202;
    background:#f2f2f2;
    padding-top: 1.95rem;
  }
  .buy_for{
    @include sc(.6rem, #666);
    line-height: 2rem;
    padding-left: 0.7rem;
    span{
      font-weight: bold;
    }
  }
  .vip_prerogative{
    background-color: #fff;
    padding-left: .7rem;
    margin-bottom: 0.5rem;
    .vip_detail{
      display: flex;
      border-bottom: 1px solid #f5f5f5;
      padding: .8rem 0;
      .vip_detail_left{
        margin-right: .6rem;
        img{
          @include wh(1.6rem, 1.8rem);
        }
      }
      .vip_detail_right{
        h4{
          @include sc(.7rem, #333);
          font-weight: normal;
        }
        p{
          @include sc(.5rem, #999);
        }
      }
    }
  }
  .apply_vip{
    background-color: #fff;
    padding-left: .7rem;
    margin-bottom: 0.5rem;
    .apply_vip_buy{
      @include fj;
      align-items: center;
      padding-right: .7rem;
      font-size: .7rem;
      line-height: 2.6rem;
      .apply_vip_buy_left{
        span:nth-of-type(2){
          font-weight: bold;
          color: #f60;
        }
      }
      .apply_vip_buy_right{
        border: 0.025rem solid #f60;
        border-radius: 0.2rem;
        line-height: 1.2rem;
        height: 1.2rem;
        width: 2.6rem;
        text-align: center;
        @include sc(.6rem, #f60);
      }
    }
  }
  .header_style{
    @include fj;
    background-color: #fff;
    line-height: 2rem;
    border-bottom: 1px solid #f5f5f5;
    padding-right: .7rem;
    .header_left{
      @include sc(.7rem, #333);
    }
    .header_right{
      display: flex;
      align-items: center;
      span{
        @include sc(.6rem, #999);
        margin-right: .2rem;
      }
      svg{
        @include wh(.5rem, .5rem);
      }
    }
  }
  .common_style{
    padding: 0 .7rem;
    margin-bottom: 0.5rem;
  }
  .router-slid-enter-active, .router-slid-leave-active {
    transition: all .4s;
  }
  .router-slid-enter, .router-slid-leave-active {
    transform: translate3d(2rem, 0, 0);
    opacity: 0;
  }
</style>



